import React from "react";
import { useFirebaseNode } from "../hooks/useFirebaseNode";

import Layout from "../components/layout"

import Button from 'react-bootstrap/Button'
import Collapse from 'react-bootstrap/Collapse'
import Card from 'react-bootstrap/Card'



export default function StudentsByTheme() {
    const { data: usersCraft, loading, error } = useFirebaseNode('usersCraft');

    if (!!loading || !usersCraft) { return <Layout><div>...</div></Layout> }

    if (!!error) { return <Layout><div>:-(</div></Layout> }

    let allposts = [];
    Object.keys(usersCraft).forEach(user_key => {
        if (!!usersCraft?.[user_key]?.posts) {
            Object.keys(usersCraft[user_key].posts).forEach(post_key => {
                if (!!usersCraft[user_key].posts?.[post_key]) {
                    let post = usersCraft[user_key].posts[post_key];
                    allposts.push({
                        theme: post?.theme,
                        title: post?.title,
                        user_login: user_key,
                        user: post?.user
                    });
                }
            })
        }
    });

    //  console.log(allposts);

    const uniqueThemes = !!allposts ? [...new Set(allposts.map(item => item.theme))] : []; // [ 'A', 'B']

    // console.log(uniqueThemes);

    return <Layout>
        {uniqueThemes.map((theme, index) => <ResultsByTheme key={theme} theme={theme} index={index} theme_posts={allposts.filter(item => item.theme === theme)} />)}
    </Layout>
}

function ResultsByTheme(props) {
    const [open, setOpen] = React.useState(false);

    const uniqueUsers = !!props.theme_posts ? [...new Set(props.theme_posts.map(item => item.user_login))] : [];
    return (
        <Card className="m-3">
            <Card.Header>{props.theme}</Card.Header>
            <Card.Body>
                <Button
                    onClick={() => setOpen(!open)}
                    aria-controls={"collapse-text" + props.index}
                    aria-expanded={open}
                    variant="outline-secondary"
                >
                    {props.theme}
                </Button>
                <Collapse in={open}>
                    <div id={"collapse-text" + props.index}>
                        {props.theme_posts.length}
                        {uniqueUsers.map(user => <div key={user}>{user}</div>)}
                    </div>
                </Collapse>
            </Card.Body>
        </Card>

    );
}